  }
  &.sidenav-toggled.sidenav-toggled-open .app-sidebar {
    background: url(../../assets/img/backgrounds/02.png) !important;
    background-size: cover !important;
  }
}
.leftbgimage3 {
  .app-sidebar {
    background: url(../../assets/img/backgrounds/03.png) !important;
    background-size: cover !important;
  }
  &.sidenav-toggled.sidenav-toggled-open .app-sidebar {
    background: url(../../assets/img/backgrounds/03.png) !important;
