}
.leftbgimage5 {
  .app-sidebar {
    background: url(../../assets/img/backgrounds/05.png) !important;
    background-size: cover !important;
  }
  &.sidenav-toggled.sidenav-toggled-open .app-sidebar {
    background: url(../../assets/img/backgrounds/05.png) !important;
    background-size: cover !important;
  }
}

.leftbgimage1,
.leftbgimage2,
.leftbgimage3,
.leftbgimage4,
.leftbgimage5 {
  .app-sidebar {
    width: 240px;
    border-inline-end: 1px solid $border;
    transition: all 0.3s ease-in-out;
    @media screen and (min-width: 992px) {
      &.horizontal-main {
        width: 100%;
        border-block-end: 0 solid $border !important;

        &:before {
          position: absolute;
          width: 100%;
          height: 103%;
          top: -1px;
        }
      }
    }
    @media screen and (max-width: 991px) {
      &:before {
        height: 150%;
      }
    }

    &:before {
      content: "";
      position: fixed;
      background: $white;
      height: 100%;
      width: 240px;
      z-index: -1;
      top: 0;
      bottom: 0;
      opacity: 0.8;
      transition: all 0.3s ease-in-out;
    }
  }
  &.dark-theme {
    .app-sidebar {
      border-inline-end: 1px solid $dark-border;

      &:before {
        background: $dark-theme;
      }
    }
  }
  &.light-menu {
    .app-sidebar {
      border-inline-end: 1px solid $dark-border;

      &:before {
        background: $white;
      }
    }
  }
  &.color-menu {
    .app-sidebar {
      border-inline-end: 1px solid $dark-border;

      &:before {
        background: $primary;
      }
    }
  }
  &.dark-menu {
    .app-sidebar {
      border-inline-end: 1px solid $dark-border;

      &:before {
        background: $dark-theme;
      }
    }
  }
  &.gradient-menu {
    .app-sidebar {
      border-inline-end: 1px solid $dark-border;

      &:before {
        background: $primary-gradient;
      }
    }
  }
}


.leftbgimage1 .bg1,
.leftbgimage2 .bg2,
.leftbgimage3 .bg3,
.leftbgimage4 .bg4,
.leftbgimage5 .bg5 {
  &.wscolorcode1 {
    border: 1px solid $primary;
  }
}
@media (min-width: 992px) {
  .leftbgimage1,
  .leftbgimage2,
  .leftbgimage3,
  .leftbgimage4,
  .leftbgimage5 {
    &.app.sidenav-toggled {
      .app-sidebar:before {
        width: 80px;
      }

      &.double-menu, &.double-menu-tabs {
        .app-sidebar:before {
          width: 70px;
        }
      }

      &.sidenav-toggled-open .app-sidebar:before {
        width: 240px;
      }
    }

    &.icontext-menu.app.sidenav-toggled {
      .app-sidebar:before {
        width: 105px !important;
      }

      &.sidenav-toggled-open .app-sidebar:before {
        width: 240px !important;
      }
    }

    &.hover-submenu.app.sidenav-toggled .app-sidebar:before,
    &.hover-submenu1.app.sidenav-toggled .app-sidebar:before {
      width: 110px !important;
    }
  }
}

/***********Leftmenu Image Styles****************/

/***********Boxed Styles****************/
@media (min-width: 1400px) {
  body.layout-boxed {
    background: $background;
    .page {
      width: 1400px;
      margin: 0 auto;
      background: $background;
      position: relative;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
      flex-direction: column;
      -ms-flex-pack: center;
      justify-content: center;
      min-height: 100vh;
      box-shadow: $black-3 0px 5px 0px 0px, $black-3 0px 5px 25px 0px;
    }
    &.horizontal .hor-header .container,
    &.horizontal .horizontal-main .container,
    &.horizontal .main-content.horizontal-content .container {
      max-width: 95% !important;
    }
    .sticky-pin .horizontal-main {
      max-width: 1400px;
      margin: 0;
    }
    .main-header {
      max-width: 1400px;
      margin: 0 auto;
      inset-inline-start: 0;
      inset-inline-end: 0;
      top: 0;
    }
    .main-content .container,
    .main-header > .container,
    .horizontal-mainwrapper.container {
      padding-inline-end: 0.75rem;
      padding-inline-start: 0.75rem;
    }
    .horizontalMenu > .horizontalMenu-list > li > a {
      padding: 15px 11px 15px 11px;
    }
    .page.bg-primary-transparent,
    .main-error-wrapper.page {
      background: $primary-3 !important;
    }
    .app-sidebar {
      inset-inline-start: auto !important;
    }
    .main-content-left-chat .main-nav-line-chat .nav-link + .nav-link {
      margin-inline-start: 0;
    }
    .main-nav-line-chat {
      padding: 0 17px;
    }
    .app-sidebar .main-sidebar-header {
      inset-inline-start: auto;
      inset-inline-end: auto;
    }
    &.dark-theme {
      background: $dark-body !important;
      .page {
        background: $dark-body;
        box-shadow: $white-1 0px 5px 0px 0px, $white-1 0px 5px 25px 0px;
      }
      .page.bg-primary-transparent,
      .main-error-wrapper.page {
        background: $dark-body !important;
      }
    }
  }
}
/***********Boxed Styles****************/

/***********Scroll-layout Styles****************/

/*--- Scrollabel-header ----*/
@media (max-width: 991px) {
  .scrollable-layout .responsive-navbar.navbar {
    .navbar-collapse {
      position: absolute !important;
    }

    position: inherit !important;
  }
}
@media (min-width: 992px) {
  .horizontal.scrollable-layout .app-sidebar {
    position: relative;
    max-width: 100% !important;
  }

  .horizontal.scrollable-layout .main-header {
    position: relative;
    max-width: 100% !important;
  }
}
.scrollable-layout {
  .main-header.side-header {
    position: absolute !important;
    max-width: 100% !important;
  }

  .app-sidebar,
  .main-sidebar-header {
    position: absolute;
  }

  .page {
    position: relative;
  }

  .sticky.sticky-pin {
    position: inherit !important;
  }
}

/***********Scroll-layout Styles****************/

/***********Horizontal Styles****************/
@media (min-width: 992px) {
  .centerlogo-horizontal {
    &.horizontal {
      .hor-header {
        .main-header-left {
          .responsive-logo {
            display: block;
            position: absolute;
            inset-inline-start: 0;
            inset-inline-end: 0;
            margin: 0 auto;
            text-align: center;
            top: 17px;
          }
        }
      }
    }
  }
}

@media (min-width: 992px) {
  .scrollable-layout.double-menu.app .app-sidebar, .scrollable-layout.double-menu-tabs.app .app-sidebar {
      position: absolute !important;
  }
}
/***********Switcher-styles End****************/
